.book-a-tour {
    background-color: $title-green;
    padding-top: 60px;
    padding-bottom: 122px;


    @include respond-to("small") {
        padding-bottom: 122px;
    }

    @include respond-to("large") {
        // padding-left: 173px;
    }

    &__left {

        max-width: 416px;
        position: relative;




        h2 {
            font-weight: 500;
            font-size: 42px;
            line-height: 50px;
            color: white;
            margin-bottom: 24px;

            @include respond-to("medium") {
                font-size: 54px;
                line-height: 56px;
            }
        }
    }



    .paragraph-regular {
        color: white;
    }

    &__button {
        padding-top: 32px;
    }

    &__flex {

        img {
            border-radius: 12px;
        }

        @include respond-to("small") {
            display: flex;
            justify-content: space-around;
            gap: 138px;
        }


    }

    &__back-photo {
        position: relative;
        margin-top: 30px;
        width: 100%;
        display: flex;
        align-items: end;
        justify-content: end;

        img {
            width: 80%;
            object-fit: cover;
        }

        @include respond-to("small") {
            margin-top: 0;

            img {
                width: 100%;
            }
        }
    }

    &__front-photo {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;

        img {
            width: 60%;
        }

        @include respond-to("small") {
            left: -30%;
            top: 30%;

            img {
                width: 80%;
            }
        }
    }
}