.testimonial-photos {
    margin-bottom: 14px;
    &__flex {
        display: flex;
        gap: 14px;
        img {
            height: 100%;
            width: 100%;
            border-radius: 17px;
            aspect-ratio: 1;
            object-fit: cover;
        }
    }

    &__single-photo {
        width: 100%;
    }

    
}